import React, { Component } from "react";
import factory from "../ethereum/factoryContract";

export default class projectIndex extends Component {
  static async getInitialProps() {
    const projects = await factory.methods.getDeployedCampaigns().call();
    return { projects };
  }

  onSubmit = e => {
    e.preventDefault();
    const contribution = e.target;
    console.log(contribution);
  };

  render() {
    return (
      <div>
        <h1>This is the homepage with all the active projects.</h1>
        <h3>These are addresses of all the deployed projects:</h3>
        {this.props.projects}
        <br />
        <h3>Deploy a new project in the form below:</h3>
        <form>
          <p>Please make a contribution</p>
          <input name="contribution" />
          <button onSubmit={e => this.handleSubmit(e)}>Contribute</button>
        </form>
      </div>
    );
  }
}
